import "./styles.css";

const Handlebars = require("handlebars");

const ContentClient = require("dc-delivery-sdk-js").ContentClient;
// connect to the Delivery SDK
const client = new ContentClient({
  hubName: "ampproduct-doc"
});

const example_key = "example-key";
client
  .getContentItemByKey(example_key)
  .then((content) => {
    console.log(JSON.stringify(content.body));
    renderContent(content.body);
  })
  .catch((error) => {
    console.log("content not found", error);
  });

function renderContent(content) {
  var templateSource = document.getElementById("tutorial-template").innerHTML;
  var template = Handlebars.compile(templateSource);

  // render the content using the template
  var html = template(content);

  document.body.innerHTML = html;
}
